.navigation {
  background-image: linear-gradient(to right, #f7da5b, #ff6200);
  ul {
    display: flex;
    margin-right: 10px;
    margin-left: 10px;
    a {
      text-decoration: none;
      font-size: 25px;
      padding: 10px;

      li {
        color: $myblue;
        transition: 0.2s;
        cursor: pointer;

        &:hover {
          color: $darkblue;
        }
      }
    }

    .nav-active {
      position: relative;

      &::after {
        content: "";
        height: 4px;
        width: 0;
        background: $myblue;
        position: absolute;
        border-radius: 10px;
        bottom: 0px;
        left: 50%;
        transform: translateX(-50%);
        -webkit-animation: anim 0.3s ease forwards;
        animation: anim 0.6s ease forwards;

        @-webkit-keyframes anim {
          to {
            width: 100%;
          }
        }
        @keyframes anim {
          to {
            width: 100%;
          }
        }
      }
    }
  }
}
