.accueil {
    display: flex;
    flex-direction: column; // Empile verticalement
    align-items: center; // Centre horizontalement
    margin: 50px; // Diminuez la marge globale si nécessaire
  
    h1 {
      text-align: center;
      width: 100%; // Assurez-vous que h1 prend toute la largeur pour le centrer correctement
      margin-bottom: 40px; // Espace sous le h1
    }
  
    .bulle {
      border-radius: 25px; // Coins arrondis pour un effet de bulle
      background-color: #e7e7e4; // Couleur de fond pour les bulles
      padding: 20px; // Espace intérieur autour du contenu de la bulle
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); // Ombre pour un effet de profondeur
      margin: 20px 0; // Ajoute un peu d'espace entre les bulles
      width: calc(33.333% ); // Largeur des bulles ajustée à un tiers de l'espace disponible moins 60px pour les marges
  
      // Alignements spécifiques pour les bulles à gauche et à droite
      &.left {
        margin-right: 1000px; // Maintient la marge à droite pour les bulles de gauche
        margin-left: 150px; // Marge à gauche pour l'espacement
      }
  
      &.right {
        margin-left: 1000px; // Maintient la marge à gauche pour les bulles de droite
        margin-right: 150px; // Marge à droite pour l'espacement
      }
    }
  
    @media (max-width: 767px) { // Pour les petits écrans, vous pouvez ajuster la taille des bulles et les centrer
      .bulle {
        width: 80%; // Largeur plus grande pour les petits écrans
        margin: 20px auto; // Centrer les bulles horizontalement avec une marge automatique
      }
    }
  }
  